import React from "react";
import { Link, NavLink } from "react-router-dom";

const NavBar = () => {
  return (
    <nav className="navbar navbar-expand-lg navbar-light bg-light">
      <Link className="navbar-brand" to="/dashboard">
        WeMA
      </Link>
      <button
        className="navbar-toggler"
        type="button"
        data-toggle="collapse"
        data-target="#navbarNavAltMarkup"
        aria-controls="navbarNavAltMarkup"
        aria-expanded="false"
        aria-label="Toggle navigation"
      >
        <span className="navbar-toggler-icon" />
      </button>
      <div className="collapse navbar-collapse" id="navbarNavAltMarkup">
        <div className="navbar-nav">
          <NavLink className="nav-item nav-link" to="/dashboard">
            Dashboard
          </NavLink>
          <NavLink className="nav-item nav-link" to="/invoice">
            Invoice
          </NavLink>
          <NavLink className="nav-item nav-link" to="/customer">
            Customer
          </NavLink>
          <NavLink className="nav-item nav-link" to="/master">
            Master
          </NavLink>
          <NavLink className="nav-item nav-link" to="/report">
            Report
          </NavLink>
        </div>
      </div>
    </nav>
  );
};

export default NavBar;
