import React, { Component } from "react";
import NavBar from "../common/navBar";
import SubNavBar from "./master/subNavBar";

export class Master extends Component {
  render() {
    return (
      <React.Fragment>
        <NavBar />
        <div className="row">
          <div className="col-2">
            <SubNavBar />
          </div>
          <div className="col">Hello world</div>
        </div>
        <main className="container" />
      </React.Fragment>
    );
  }
}
